export default {
    data:[{
    name: 'label',
    type: 'String',
    default: null,
    desc: '`sc-radio`value'
  },{
    name: 'v-model',
    type: '',
    default: null,
    desc: '`sc-radio`'
  },{
    name: 'disabled',
    type: 'Boolean',
    default: 'false',
    desc: '`sc-radio`disabled'
  },{
    name: 'change',
    type: '',
    default: null,
    desc: '`sc-radio-group`@changev-model'
  }]
}


