<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <sc-modal name="modal1" position="top" bgClose>
                <template slot="modal-content">
                    
                </template>
            </sc-modal>
            <sc-modal name="modal2" position="top" 
                header title="" close>
                <template slot="modal-content">
                    {{content}}
                </template>
                <template slot="modal-footer">
                    <sc-button type="plain" @click.prevent.stop="cancle2"></sc-button>
                    <sc-button type="primary"></sc-button>
                </template>
            </sc-modal>
            
            <sc-button type="primary" @click="handleClick1"></sc-button>
            <sc-button type="success" @click="handleClick2">,</sc-button>
        </code-example>
        <code-example title="" :code="code2" :description="description2">
            <sc-modal name="modal3" :position="position"
                header title="" close>
                <template slot="modal-content">
                    
                </template>
                <template slot="modal-footer">
                    <sc-button type="plain" @click.prevent.stop="cancle3"></sc-button>
                    <sc-button type="primary"></sc-button>
                </template>
            </sc-modal>

            <sc-button type="plain-primary" @click="handlePosition('center')">center</sc-button>
            <sc-button type="plain-success" @click="handlePosition('bottom')">bottom</sc-button>
            <sc-button type="plain-warning" @click="handlePosition('left')">left</sc-button>
            <sc-button type="plain-danger" @click="handlePosition('right')">right</sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code3" :description="description3">
            <sc-modal name="modal4" :size="size"
                header title="" close>
                <template slot="modal-content">
                    
                </template>
                <template slot="modal-footer">
                    <sc-button type="plain" @click.prevent.stop="cancle4"></sc-button>
                    <sc-button type="primary"></sc-button>
                </template>
            </sc-modal>
        
            <sc-button type="primary" @click="handleSize('md')"></sc-button>
            <sc-button type="success" @click="handleSize('lg')"></sc-button>
            <sc-button type="danger" @click="handleSize('sm')"></sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code4" :description="description4">
            <sc-modal name="modal5" position="top"
                header title="" close draggable>
                <template slot="modal-content">
                    
                </template>
                <template slot="modal-footer">
                    <sc-button type="plain" @click.prevent.stop="cancle5"></sc-button>
                    <sc-button type="primary"></sc-button>
                </template>
            </sc-modal>
            <sc-button type="warning" @click="handleClick5"></sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code5" :description="description5">
            <sc-modal name="modal6" position="top"
                header title="" close resizable :minWidth="50" :minHeight="50">
                <template slot="modal-content">
                    
                </template>
                <template slot="modal-footer">
                    <sc-button type="plain" @click.prevent.stop="cancle6"></sc-button>
                    <sc-button type="primary"></sc-button>
                </template>
            </sc-modal>
            <sc-button type="plain-success" @click="handleClick6"></sc-button>
        </code-example>
        <code-table title="API" :data="dataArray.data"></code-table>
    </div>
</template>

<script>
    import dataArray from './dataArray'
    import description from './description'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    import code4 from './code4.md'
    import code5 from './code5.md'
    export default {
        data() {
            return {
                dataArray: dataArray,
                description1: description.description1,
                code1: code1,
                description2: description.description2,
                code2: code2,
                description3: description.description3,
                code3: code3,
                description4: description.description4,
                code4: code4,
                description5: description.description5,
                code5: code5,
                position: 'top',
                size: 'md',
                content: '',
                interval: null
            }
        },
        methods: {
            handleClick1() {
                this.$modal.show("modal1", {})
            },
            cancle1() {
                this.$modal.hide("modal1")
            },
            handleClick2() {
                this.interval = setInterval(()=> {
                    this.content += ''
                },500)
                //
                this.$modal.show("modal2", {})
            },
            cancle2() {
                if(this.interval) {
                    clearInterval(this.interval)
                }
                this.$modal.hide("modal2")
            },
            handleClick3() {
                this.$modal.show("modal3", {})
            },
            cancle3() {
                this.$modal.hide("modal3")
            },
            handleClick4() {
                this.$modal.show("modal4", {})
            },
            cancle4() {
                this.$modal.hide("modal4")
            },
            handleClick5() {
                this.$modal.show("modal5", {})
            },
            cancle5() {
                this.$modal.hide("modal5")
            },
            handleClick6() {
                this.$modal.show("modal6", {})
            },
            cancle6() {
                this.$modal.hide("modal6")
            },
            handlePosition(pos) {
                this.position = pos
                this.handleClick3()
            },
            handleSize(size) {
                this.size = size
                this.handleClick4()
            }
        },
        mounted() {
            
        }
    }
</script>


