export default {
    data:[{
    name: 'name',
    type: 'String',
    default: null,
    desc: '`sc-modal``name`js'
  },{
    name: 'bgClose',
    type: 'Boolean',
    default: false,
    desc: '`sc-modal``bgClose`'
  },{
    name: 'size',
    type: 'String',
    default: 'md',
    desc: '`sc-modal`lg,md,sm'
  },{
    name: 'position',
    type: 'String',
    default: 'top',
    desc: '`sc-modal`center,bottom,left,right'
  },{
    name: 'header',
    type: 'Boolean',
    default: false,
    desc: '`sc-modal`'
  },{
    name: 'resizable',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'close',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'title',
    type: 'String',
    default: null,
    desc: ''
  },{
    name: 'draggable',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'minWidth',
    type: 'Number',
    default: null,
    desc: ''
  },{
    name: 'minHeight',
    type: 'Number',
    default: null,
    desc: ''
  }]
}


