export default {
    data:[{
    name: 'type',
    type: 'String',
    default: 'defalut',
    desc: '`sc-button`type'
  },{
    name: 'disabled',
    type: 'Boolean',
    default: 'false',
    desc: ''
  },{
    name: 'icon',
    type: 'String',
    default: null,
    desc: '`sc-button```` sc-icon ```,'
  },{
    name: 'iconRight',
    type: 'Boolean',
    default: 'false',
    desc: 'true,'
  },{
    name: 'size',
    type: 'String',
    default: 'md',
    desc: '`sc-button`size(lg,md,sm,mi)'
  },{
    name: 'iconSize',
    type: 'String,Number',
    default: '14',
    desc: '`sc-button`iconSize'
  },{
    name: 'nativeType',
    type: 'String',
    default: 'button',
    desc: '`sc-button`nativeType(button,submit,reset)'
  },{
    name: 'href',
    type: 'String',
    default: null,
    desc: '`sc-button`href'
  },{
    name: 'click',
    type: '',
    default: null,
    desc: '`sc-button`@click'
  }]
}


