<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <sc-form>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item>
                    <sc-button type="primary"></sc-button>
                    <sc-button type="default"></sc-button>
                </sc-form-item>
            </sc-form>
        </code-example>
        <br/>
        <code-example title="" :code="code2" :description="description2">
            <sc-form inline>
                <sc-form-item label="">
                    <sc-row>
                        <sc-input placeholder=""></sc-input>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-input placeholder=""></sc-input>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-input placeholder=""></sc-input>
                    </sc-row>
                </sc-form-item>

                <sc-form-item>
                    <sc-button type="primary"></sc-button>
                    <sc-button type="default"></sc-button>
                </sc-form-item>
            </sc-form>
        </code-example>
        <br/>
        <code-example title="label" :code="code3" :description="description3">
            <div class="margin-bottom-5 text-right">
                <sc-button-group>
                    <sc-button type="primary" @click="position='left'"></sc-button>
                    <sc-button type="primary" @click="position='right'"></sc-button>
                    <sc-button type="primary" @click="position='top'"></sc-button>
                </sc-button-group>
            </div>
            <sc-form :label-position="position">
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item>
                    <sc-button type="primary"></sc-button>
                    <sc-button type="default"></sc-button>
                </sc-form-item>
            </sc-form>
        </code-example>
        <br>
        <code-example title="" :code="code4" :description="description4">
            <sc-form @submit="submitClick">
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input v-model="name" placeholder=" ()"
                            v-sc-validate.required="'name'"
                            :name-validate="nameValidate"
                            name-message=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input v-sc-validate.required="['age','number']" v-model="age" placeholder=" (0<X<=100)" required-message=""
                            :age-validate="ageValidate" age-message="0100"
                            :number-validate="numberValidate" number-message="number"></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item label="">
                    <sc-row>
                        <sc-col :span="12">
                            <sc-input placeholder=""  type="textarea" v-model="work"
                            v-sc-validate.required="'work'" :work-validate="promiseValidate"
                            :work-message="workMessage"
                            ></sc-input>
                        </sc-col>
                    </sc-row>
                </sc-form-item>
                <sc-form-item>
                    <sc-button type="primary" nativeType="submit"></sc-button>
                    <sc-button type="default"></sc-button>
                </sc-form-item>
            </sc-form>
        </code-example>
        <code-table title="API" :data="dataArray.data"></code-table>
    </div>
</template>

<script>
    import description from './description'
    import dataArray from './dataArray'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    import code4 from './code4.md'
    export default {
        data() {
            return {
                dataArray: dataArray,
                description1: description.description1,
                code1: code1,
                description2: description.description2,
                code2: code2,
                description3: description.description3,
                code3: code3,
                description4: description.description4,
                code4: code4,
                position: null,
                name: null,
                age: null,
                work: null,
                workMessage: ''
            }
        },
        methods: {
            nameValidate(model) {
                var reg = /^[\u4e00-\u9fa5]+$/
                if(reg.test(model)) {
                    return true
                }
                return false
            },
            ageValidate(model) {
                //true
                if(model<=100&&/^[1-9]\d*$/.test(model)) {
                    return true
                }
                return false
            },
            numberValidate(model) {
                if(/^[0-9]*$/.test(model)){
                    return true
                }
                return false
            },
            promiseValidate(model) {
                //true
                let promise = new Promise((resolve,reject)=> {
                    setTimeout(()=> {
                        resolve([true,null])
                        this.work = ""
                    },2000)
                })
                //
                promise.result = false
                return promise
            },
            submitClick() {
                alert('')
            }
        },
        mounted() {
        }
    }
</script>



