<template>
    <div class="content-wrapper">
        <code-example title="" :code="code" :description="description1">
            <sc-button type="primary" @click="handleConfirm"></sc-button>
            <sc-button type="warning" @click="handleWarning"></sc-button>
            <sc-button type="danger" @click="handleError"></sc-button>
            <sc-button type="info" @click="handleInfo"></sc-button>
            <sc-button type="success" @click="handleSuccess"></sc-button>
            <sc-button  @click="handlePrompt"></sc-button>
        </code-example>
    </div>
</template>

<script>
    import description from './description'
    import code from './code.md'
    export default {
        data() {
            return {
                description1: description.description1,
                code: code
            }
        },
        methods: {
            handleConfirm() {
                this.$alert.confirm('?').then((data)=> {
                    alert('')
                },(data)=> {
                    alert('')
                })
            },
            handleWarning() {
                this.$alert.warning(', ?').then((data)=> {
                    //alert('warning')
                },(data)=> {
                    //alert('warning')
                })
            },
            handleError() {
                this.$alert.error('').then((data)=> {
                    //alert('error')
                })
            },
            handleInfo() {
                this.$alert.info('').then((data)=> {
                    //alert('info')
                })
            },
            handleSuccess() {
                this.$alert.success('').then((data)=> {
                    //alert('success')
                })
            },
            handlePrompt() {
                this.$alert.prompt('').then((data)=> {
                    alert(''+data)
                })
            }
        }
    }
</script>


