<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <sc-collapse-group @clickItem="handleClick">
                <sc-collapse title="1">{{dataContent}}</sc-collapse>
                <sc-collapse title="2">{{dataContent}}</sc-collapse>
                <sc-collapse title="3">{{dataContent}}</sc-collapse>
            </sc-collapse-group>
        </code-example>
        <br>
        <code-example title="" :code="code2">
            <sc-collapse-group accordion>
                <sc-collapse title="1">{{dataContent}}</sc-collapse>
                <sc-collapse title="2">{{dataContent}}</sc-collapse>
                <sc-collapse title="3">{{dataContent}}</sc-collapse>
            </sc-collapse-group>
        </code-example>
        <br>
        <code-example title="" :code="code3">
            <sc-collapse-group type="primary" v-model="primaryValue">
                <sc-collapse title="1" name="label1">{{dataContent}}</sc-collapse>
                <sc-collapse title="2" name="label2">{{dataContent}}</sc-collapse>
                <sc-collapse title="3" name="label3">{{dataContent}}</sc-collapse>
            </sc-collapse-group>
        </code-example>
        <br>
        <code-example title="" :code="code4">
            <sc-collapse-group accordion type="success" v-model="successValue">
                <sc-collapse title="1" name="label1">{{dataContent}}</sc-collapse>
                <sc-collapse title="2" name="label2">{{dataContent}}</sc-collapse>
                <sc-collapse title="3" name="label3">{{dataContent}}</sc-collapse>
            </sc-collapse-group>
        </code-example>
        <code-table title="API" :data="dataArray.data"></code-table>
    </div>
</template>
<script>
    import description from './description'
    import dataArray from './dataArray'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    import code4 from './code4.md'
    export default {
        data() {
            return {
                dataArray: dataArray,
                description1: description.description1,
                code1: code1,
                code2: code2,
                code3: code3,
                code4: code4,
                dataContent: 'overflowscrollHeight,scrollHeightoverflow',
                primaryValue: ['label1','label2'],
                successValue: 'label3'
            }
        },
        methods: {
            handleClick(instance) {
                //alert(''+instance)
                //console.log("")
            }
        },
        mounted() {
            // setInterval(()=> {
            //     this.dataContent += this.dataContent
            // },1000)
        }
    }
</script>


