export default {
    data:[{
    name: 'value',
    type: 'String',
    default: null,
    desc: '`sc-icon`value'
  },{
    name: 'size',
    type: 'String,Number',
    default: 14,
    desc: '`sc-icon`sizepx'
  },{
    name: 'color',
    type: 'String',
    default: '#000',
    desc: '`sc-icon`color#000red'
  },{
    name: 'className',
    type: 'String',
    default: null,
    desc: '`sc-icon`className'
  },{
    name: 'click',
    type: '',
    default: null,
    desc: '`sc-icon`click'
  }]
}


