export default {
    data:[{
    name: 'placeholder',
    type: 'String',
    default: null,
    desc: 'placeholder'
  },{
    name: 'size',
    type: 'String,Number',
    default: 'md',
    desc: '`sc-input`'
  },{
    name: 'disabled',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'showTime',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'outsideClickNone',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'change',
    type: '',
    default: null,
    desc: 'v-model'
  },{
    name: 'v-model',
    type: null,
    default: null,
    desc: 'v-model(12/12/1223:23:23)'
  },{
    name: 'showFormat',
    type: 'String',
    default: 'HH,MM,SS',
    desc: '(HH)(MM)(SS)(H,M,ssh,ss,MM)'
  }]
}


