<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <div class="margin-bottom-5">
                <sc-button type="primary" @click="setStripe"></sc-button>
                <sc-button type="success" @click="setBorder"></sc-button>
                <sc-button type="info" @click="setSmall"></sc-button>
                <sc-button type="warning" @click="setHeight"></sc-button>
            </div>
            <sc-table :data="tableData" :stripe="stripe" :border="border" :small="small"
                        :height="height">
                <sc-table-column></sc-table-column>
                <sc-table-column></sc-table-column>
                <sc-table-column></sc-table-column>
            </sc-table>
        </code-example>
        <br>
        <code-example title="" :code="code2" :description="description2">
            <sc-table :data="tableData" stripe border>
                <sc-table-column></sc-table-column>
                <sc-table-column></sc-table-column>
                <sc-table-column></sc-table-column>

                <sc-table-column compile slot-name="table-caption1"></sc-table-column>
                <template slot="table-caption1" slot-scope="subData">
                    <sc-button type="primary" size="sm" @click="setCallback(subData.data)"></sc-button>
                    <sc-button type="danger" size="sm"></sc-button>
                </template>
                <sc-table-column compile slot-name="table-caption2"></sc-table-column>
                <template slot="table-caption2" slot-scope="subData">
                    <sc-button :disabled="subData.data.name == '' " type="plain-primary" size="sm" @click="setCallback2(subData.data)">2</sc-button>
                </template>  
                
            </sc-table>
        </code-example>
        <br>
        <code-example title="" :code="code3" :description="description3">
            <sc-table :data="tableData1">
                <sc-table-column sortable label="date"></sc-table-column>
                <sc-table-column label="name"></sc-table-column>
                <sc-table-column filter label="address"></sc-table-column>
            </sc-table>
        </code-example>
        <code-table title="API" :data="dataArray.data"></code-table>
    </div>
</template>
<script>
    import description from './description'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    import dataArray from './dataArray'
    export default {
        data() {
            return {
                description1: description.description1,
                code1: code1,
                description2: description.description2,
                code2: code2,
                description3: description.description3,
                code3: code3,
                dataArray: dataArray,
                stripe: false,
                border: false,
                small: false,
                height: null,
                tableData: [{
                    date: '2017-12-11',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-13',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-14',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-12',
                    name: '',
                    address: ' 5678 '
                }],
                tableData1: [{
                    date: '2017-12-11',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-13',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-14',
                    name: '',
                    address: ' 5678 '
                }, {
                    date: '2017-12-12',
                    name: '',
                    address: '5678 '
                }]
            }
        },
        methods: {
            setStripe() {
                this.stripe = !this.stripe
            },
            setBorder() {
                this.border = !this.border
            },
            setSmall() {
                this.small = !this.small
            },
            setHeight() {
                if(this.height) {
                    this.height = null
                }else {
                    this.height = '100px'
                } 
            },
            setCallback(data) {
                alert(','+data.date+' '+data.name+' '+data.address)
            },
            setCallback2(data) {
                alert('2,'+data.date+' '+data.name+' '+data.address)
            }
        }
    }
</script>


