export default {
    data:[{
    name: 'v-model',
    type: 'String',
    default: null,
    desc: '`sc-switch`'
  },{
    name: 'on-text,off-text',
    type: 'String',
    default: null,
    desc: ''
  },{
    name: 'on-value,off-value',
    type: 'String',
    default: null,
    desc: 'truefalse'
  },{
    name: 'on-color,off-color',
    type: 'String',
    default: false,
    desc: ''
  },{
    name: 'disabled',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'change',
    type: '',
    default: false,
    desc: 'value'
  }]
}


