export default {
    data:[{
    name: 'v-model',
    type: 'String',
    default: null,
    desc: 'v-modelkey'
  },{
    name: 'disabled',
    type: 'Boolean',
    default: 'false',
    desc: ''
  },{
    name: 'data',
    type: 'Array',
    default: '[]',
    desc: 'datakeykeyName'
  },{
    name: 'keyName',
    type: 'String',
    default: 'key',
    desc: 'true,'
  },{
    name: 'size',
    type: 'String',
    default: 'md',
    desc: 'size`sc-input`'
  },{
    name: 'icon',
    type: 'String',
    default: '',
    desc: 'input'
  },{
    name: 'iconClear',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'position',
    type: 'String',
    default: 'button',
    desc: 'bottom(top,bottom,left,right)'
  },{
    name: 'getData',
    type: 'Function',
    default: null,
    desc: 'getDatadata'
  },{
    name: 'dataLoading',
    type: 'Boolean',
    default: false,
    desc: 'dataLoading'
  },{
    name: 'customItem',
    type: 'String',
    default: '',
    desc: 'name'
  }]
}


