export default {
    data:[{
    name: 'v-model',
    type: 'String',
    default: null,
    desc: 'v-modelloading'
  },{
    name: 'show-percent',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'type',
    type: 'String',
    default: 'primary',
    desc: 'loadingprimary,danger,warning,success,info'
  },{
    name: 'striped',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'striped-active',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'size',
    type: 'String',
    default: 'md',
    desc: 'lg,md,sm,mi,m'
  }]
}


