export default {
    data:[{
    name: 'size',
    type: 'String',
    default: 'md',
    desc: 'xl,lg,md,sm,xs'
  },{
    name: 'type',
    type: 'String',
    default: 'default',
    desc: 'circular,default,circular-wider,circular2,ball-pulse,line-scale'
  },{
    name: 'color',
    type: 'String',
    default: 'primary',
    desc: 'primary,warning,danger,success,info,#000,yellow'
  },{
    name: 'visible',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'clickClose',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'target',
    type: 'String',
    default: null,
    desc: 'jstargetclassid'
  }]
}


