<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <sc-button></sc-button>
            <sc-button type="primary"></sc-button>
            <sc-button type="success"></sc-button>
            <sc-button type="danger"></sc-button>
            <sc-button type="warning"></sc-button>
            <sc-button type="info"></sc-button>
            <sc-button type="info" :disabled="true"></sc-button>

            <div class="margin25"></div>
            <sc-button type="plain"></sc-button>
            <sc-button type="plain-primary"></sc-button>
            <sc-button type="plain-success"></sc-button>
            <sc-button type="plain-danger"></sc-button>
            <sc-button type="plain-warning"></sc-button>
            <sc-button type="plain-info"></sc-button>
            <sc-button :disabled="true" type="plain"></sc-button>

            <div class="margin25"></div>
            <sc-button type="text"></sc-button>
            <sc-button type="text-primary"></sc-button>
            <sc-button type="text-success"></sc-button>
            <sc-button type="text-danger"></sc-button>
            <sc-button type="text-warning"></sc-button>
            <sc-button type="text-info"></sc-button>
            <sc-button :disabled="true" type="text-success"></sc-button>

            <div class="margin25"></div>
            <sc-button type="text" icon="bell-o" iconSize="30"></sc-button>
            <sc-button type="text-primary" icon="bell-o" iconSize="30"></sc-button>
            <sc-button type="text-success" icon="bell-o" iconSize="30"></sc-button>
            <sc-button type="text-danger" icon="bell-o" iconSize="30"></sc-button>
            <sc-button type="text-warning" icon="bell-o" iconSize="30"></sc-button>
            <sc-button type="text-info" icon="bell-o" iconSize="30"></sc-button>
            <sc-button :disabled="true" type="text-primary" icon="bell-o" iconSize="30"></sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code2" :description="description2">
            <sc-button> <sc-icon value="bell-o"/></sc-button>
            <sc-button :disabled="true" type="primary" icon="bell-o"></sc-button>
            <sc-button type="danger" icon="pencil"></sc-button>
            <sc-button type="plain-warning" icon="plus" :icon-right="true"></sc-button>
            <sc-button type="plain-info" icon="circle-o-notch fa-spin fa-3x fa-fw">loading</sc-button>
            <sc-button :disabled="true" type="primary" icon="spinner fa-spin fa-3x fa-fw"></sc-button>
            <sc-button type="primary" icon="spinner fa-pulse fa-3x fa-fw"></sc-button>
            <sc-button type="primary" icon="refresh fa-spin fa-3x fa-fw"></sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code3" :description="description3">
            <sc-button size="lg"> <sc-icon value="plus"/></sc-button>
            <sc-button type="primary" icon="bell-o"></sc-button>
            <sc-button size="sm" type="danger" icon="pencil"></sc-button>
            <sc-button size="mi" href="#/layout" type="plain-warning"> <sc-icon value="plus"/></sc-button>
        </code-example>
        <br>
        <code-example title="" :code="code4" :description="description4">
            <sc-button-group>
                <sc-button   type="plain-primary" icon="chevron-left"></sc-button>
                <sc-button   type="plain-primary"></sc-button>
                <sc-button   type="plain-primary"></sc-button>
                <sc-button   type="plain-primary" icon="chevron-right" :icon-right="true"></sc-button>
            </sc-button-group>
            <sc-button-group>
                <sc-button  type="primary" icon="chevron-left"></sc-button>
                <sc-button  type="primary" icon="pencil"></sc-button>
                <sc-button  type="primary" icon="pencil"></sc-button>
                <sc-button  type="primary" icon="chevron-right"></sc-button>
            </sc-button-group>
        </code-example>
        <code-table title="API" :data="dataArray.data"></code-table>
    </div>
</template>
<script>
    import description from './description'
    import dataArray from './dataArray'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    import code4 from './code4.md'
    export default {
        data() {
            return {
                description1: description.description1,
                dataArray: dataArray,
                code1: code1,
                code2: code2,
                description2: description.description2,
                code3: code3,
                description3: description.description3,
                code4: code4,
                description4: description.description4,
            }
        }
    }
</script>


