<template>
    <div class="content-wrapper">
        <code-example title="" :code="code1" :description="description1">
            <sc-notification 
                title="" 
                type="primary"
                @close="close"
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="success"
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="info"
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="danger"
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="warning"
                >
            </sc-notification>
        </code-example>
        <br>
        <code-example title="notification" :code="code2" :description="description2">
            <sc-notification 
                title="" 
                type="primary"
                content=""
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="success"
                content=""
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="info"
                content=""
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="danger"
                content=""
                >
            </sc-notification>
            <br>
            <sc-notification 
                title="" 
                type="warning"
                content=""
                >
            </sc-notification>
        </code-example>
        <br>
        <code-example title="" :code="code3" :description="description3">
            <sc-row justify="space-between">
                <sc-col :span="2"><span class="title-middle"></span></sc-col>
                <sc-col :span="8"><sc-input v-model="title"></sc-input></sc-col>
                <sc-col :span="2"><span class="title-middle"></span></sc-col>
                <sc-col :span="8"><sc-input v-model="content"></sc-input></sc-col>
            </sc-row>
            <br/>
            <sc-row justify="space-between">
                <sc-col :span="2"><span class="title-middle"></span></sc-col>
                <sc-col :span="8">
                    <sc-select v-model="positionObj" :data="posArray"></sc-select>
                </sc-col>
                <sc-col :span="2"><span class="title-middle"></span></sc-col>
                <sc-col :span="8">
                    <sc-select v-model="typeObj" :data="typeArray">
                    </sc-select>
                </sc-col>
            </sc-row>
            <p class="margin-top-5">
                <sc-button type="primary" @click="handleConfirm"></sc-button>
            </p>
        </code-example>
    </div>
</template>

<script>
    import description from './description'
    import code1 from './code1.md'
    import code2 from './code2.md'
    import code3 from './code3.md'
    export default {
        data() {
            return {
                code1: code1,
                description1: description.description1,
                code2: code2,
                description2: description.description2,
                code3: code3,
                description3: description.description3,
                title:'',
                content: '',
                positionObj: {key:'',value:'bottomRight'},
                typeObj: {key:'',value:'primary'},
                posArray: [{key:'',value:'topLeft'}
                ,{key:'',value:'topCenter'}
                ,{key:'',value:'topRight'}
                ,{key:'',value:'bottomLeft'}
                ,{key:'',value:'bottomRight'}
                ,{key:'',value:'bottomCenter'}],
                typeArray: [{key:'',value:'info'}
                ,{key:'',value:'primary'}
                ,{key:'',value:'success'}
                ,{key:'',value:'warning'}
                ,{key:'',value:'danger'}]
            }
        },
        methods: {
            close() {
                alert('')
            },
            handleConfirm() {
                this.$notify({
                    title:this.title,
                    content: this.content,
                    position: this.positionObj.value,
                    type: this.typeObj.value,
                }).then(function() {
                    //alert("")
                })
            }
        }
    }
</script>
<style scoped>
    .title-middle {
        height: 36px;
        line-height: 36px;
    }
</style>


