export default {
    data:[{
    name: 'v-model',
    type: 'String,Array',
    default: null,
    desc: '`sc-slider`,'
  },{
    name: 'min,max',
    type: 'String,Number',
    default: null,
    desc: ''
  },{
    name: 'disabled',
    type: 'Boolean',
    default: false,
    desc: ''
  },{
    name: 'tooltip',
    type: 'String',
    default: false,
    desc: 'tooltip'
  },{
    name: 'vertical',
    type: 'Boolean',
    default: false,
    desc: ''
  }]
}


